.weather-box{
    position: absolute;
    width: 220px;
    height: 200px;
    background-color: rgba(0,0,0,0.1);
    border: 2px solid rgb(190, 190, 190);
    border-radius: 5px;
    right: 50px;
    top: 60px;
    overflow: auto;
}

.weather-box li{
    margin-left: 10px;
}

.weather-box li span{
    font-size: 8pt;
}

.choose-day-button{
    text-align: center;
}

.choose-day{
    padding: 5px 7px;
}
.choose-container{
    text-align: center;
}

.choose-container span{
    font-size: 12pt;
}
.loader .spinner {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border: 10px solid #f3f3f3;
    border-top: 10px solid rgba(0,0,0, 0.5);
    border-radius: 50%;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.error-container{
    text-align: center;
}

.error-container span{
    font-size: 12pt;
}
*{
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.weather-button{
    cursor: pointer;
    background-color: #FFFFFF;
    border: 2px solid rgb(190, 190, 190);
    border-radius: 5px;
    box-shadow: none;
}

.show-weather{
    position: absolute;
    right: 260px;
    top: 10px;
    padding: 10px 10px;
    font-weight: 900;
}

.weather-button:hover{
    background-color: rgb(240, 240, 240);
}

@media (max-width: 300px){
    .show-weather{
        top: 130px;
        right: 5px;
    }
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

